import { FormattedMessage } from "react-intl";

function Zijalka () {
    return (
        <div>
            <h3>
                <FormattedMessage
                    id = "experience.map.zijalka"
                    defaultMessage="Potoka Zijalka"
                />
            </h3>
            <p>
                <FormattedMessage
                    id = "experience.map.zijalka.text"
                    defaultMessage="The Potoka Zijalka karst cave is located high above Solava, on the slopes of Oleva at an altitude of 1,700 m. About 30,000 years ago, the Ice Age man stayed here."
                />
            </p>
            <a href="https://www.pokmuz-ce.si/" target="_blank" rel="noreferrer">
                <FormattedMessage
                    id = "experience.map.zijalka.link"
                    defaultMessage="https://www.pokmuz-ce.si/"
                />
            </a>
        </div>
    )
}

export default Zijalka; 